@use "global" as *;

.p-school {
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  background: #fff;
  padding-top: rem(55);
  padding-bottom: rem(66);
  padding-left: rem(13);
  padding-right: rem(13);
  @include mq(md) {
    padding-left: rem(22);
    padding-right: rem(22);
    margin-left: initial;
    margin-right: initial;
  }
}
.p-school + .p-school {
  margin-top: rem(50);
}
.p-school--secondary {
  @include mq(md) {
    padding-bottom: rem(117);
  }
}
.p-school__title-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: rem(10);
}

.p-school__icon {
  max-width: rem(56);
  width: 100%;
}
.p-school__icon img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
}

.p-school__title {
  text-align: center;
  font-size: rem(30);
  font-weight: 700;
  line-height: calc(30 / 30);
  color: #000;
}

.p-school__detail {
  margin-top: rem(60);
  @include mq(md) {
    margin-top: rem(48);
  }
}
.p-school__table {
  margin-top: rem(40);
  display: grid;
  max-width: rem(980);
  grid-template-columns: repeat(1, 1fr);
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include mq(md) {
    grid-template-columns: repeat(3, 1fr);
  }
}
.p-school__table--high {
  display: block;
}
.p-school__table-course + .p-school__table-course {
  margin-top: rem(30);
  @include mq(md) {
    margin-top: initial;
  }
}

.p-school__table-title {
  text-align: center;
  background: #16a88c;
  color: #fff;
  font-size: rem(18);
  font-weight: 700;
  line-height: calc(28 / 18);
  padding-top: rem(6);
  padding-bottom: rem(6);
}
tbody {
  background: #f8f8f8;
}
